import React, { useEffect } from "react";
import { NavLink } from "react-router-dom";

interface SidebarProps {
  openMenu: boolean;
  setOpenMenu: React.Dispatch<React.SetStateAction<boolean>>;
}

export const Sidebar: React.FC<SidebarProps> = ({ openMenu, setOpenMenu }) => {
  useEffect(() => {
    //Stop scrolling on body
    if (openMenu) {
      document.querySelector("body")?.setAttribute("data-scroll", "");
    }

    return () => {
      document.querySelector("body")?.removeAttribute("data-scroll");
    };
  }, [openMenu]);

  return (
    <nav
      id="sidebar"
      className="sidebar"
      data-expanded={openMenu}
      onClick={() => setOpenMenu(false)}
    >
      <div>
        <NavLink to="/coronavirus-dashboard" role="presentation">
          Overview
        </NavLink>
        <a role="presentation">
          Data Table
        </a>
      </div>

      <div className="external-links">
        <a
          href="https://www.who.int/emergencies/diseases/novel-coronavirus-2019"
          target="_blank"
        >
          WHO (COVID-19) Homepage
        </a>

        <a href="https://coronavirus.jhu.edu/map.html" target="_blank">
          Johns Hopkins COVID-19 Map
        </a>
      </div>

      <div className="sidebar__donate">
        <p>WHO's Covid-19 Response Fund</p>
        <a
          href="https://www.who.int/emergencies/diseases/novel-coronavirus-2019/donate"
          target="_blank"
        >
          Donate
        </a>
      </div>
    </nav>
  );
};
